# -*- coding: utf-8 -*-
<template>
<div>
  <h1>SC Calculator V0.0.1</h1>
  <van-form @submit="submit">
    <van-field v-model="ipim" name="ipim" label="加入物质名称" placeholder="加入物质名称" :rules="[{ required: true, message: '请填写加入物质名称' }]" />
    <van-field v-model="pH" type="number" name="pH" label="pH" placeholder="pH" :rules="[{ required: true, message: '请填写pH' }]" />
    <div style="margin: 16px;">
      <van-button round block type="info" native-type="submit">提交</van-button>
    </div>
  </van-form>
  <!-- <van-button plain type='primary' @click='submit'>Calculate!</van-button> -->
  <p v-if="input_item != '' ">出错的input:{{ input_item }}产生了-log({{ cH }})</p>
  <br />
  <p>运算结果: pH/cH<br />{{ result }}</p>
  <p>'HCl',  <br />'H2SO4',  <br />'KOH',  <br />'BaOH2',  <br />'H2S': 7.04,  <br />'HAc': 4.75,  <br />'HIO3': 0.8,  <br />'H2SO3(1)': 1.81,  <br />'H3PO4(1)': 2.12,  <br />'HCOOH': 3.75,  <br />'HClO(1)': 7.46,  <br />'H2CO3(1)': 6.37  <br /></p>
  <p>目前的问题:特别容易出现负根, 需要改进估算(目前是乱估算)方法; 没有弱碱方程; ...</p>
</div>
</template>

<script>
import {
  Form,
  Button,
  Field
} from 'vant'
export default {
  name: 'Cal',
  components: {
    [Form.name]: Form,
    [Button.name]: Button,
    [Field.name]: Field
  },
  data() {
    return {
      ipim: '',
      pH: '',
      result: '',
      input_item: ''
    }
  },
  methods: {
    submit() {
      var that = this
      that.input_item = ''
      that.result = ''
      console.log('submit!')
      that.$http.get(that.$api + 'phcal', {
        params: {
          ipim: that.ipim,
          pH: that.pH
        }
      }).then(
        (res) => {
          console.log('res', res.data)
          if (res.data.error === 0) {
            that.result = res.data.result
          } else {
            that.cH = res.data.result
            that.input_item = {
              ipim: res.data.ipim,
              pH: res.data.pH
            }
          }
        }
      )
    }
  }
}
</script>
